<template>
  <div ref="content">
    <b-row class="no-gutters mobile-display">
      <b-col cols="12">
        <div class="highLightHeaderLabel mb-3 mobile-text header-padding">
          {{ $t("ASKSERVICEAGENCY") }}
        </div></b-col
      >
    </b-row>

    <div class="highLightHeaderLabel mb-3 mobile-text web-display">
      {{ $t("ASKSERVICEAGENCY") }}
    </div>
    <b-form @submit.prevent="submit" class="">
      <b-row class="mb-4">
        <b-col cols="12" xl="12" class="buttonAllign">
          <b-row class="no-gutters mobile-display">
            <b-col cols="2">
              <b-button variant="primary" class="px-16 required-btn">{{
                $t("REQUIREDBUTTON")
              }}</b-button></b-col
            >
            <b-col cols="10">
              <label class="highLightHeaderLabel pl-1">{{
                $t("SUBSTITUTE")
              }}</label></b-col
            >
          </b-row>
          <div class="web-display">
            <b-button variant="primary" class="px-16 required-btn">{{
              $t("REQUIREDBUTTON")
            }}</b-button
            >&nbsp;&nbsp;
            <label class="highLightHeaderLabel pl-1">{{
              $t("SUBSTITUTE")
            }}</label>
          </div>
        </b-col>
        <b-col cols="6" xl="2" class="mt-3 buttonAllign">
          <b-form-select
            :options="getYearOptions"
            v-model.trim="year"
            class="optionClass dropDownButton"
          ></b-form-select>
        </b-col>
        <b-col cols="6" xl="2" class="mt-3">
          <b-form-select
            :options="getMonthOptions"
            v-model.trim="month"
            class="optionClass dropDownButton"
          ></b-form-select>
        </b-col>
      </b-row>
      <b-row>
        <b-col cols="12" xl="12" class="mb-2 buttonAllign">
          <b-row class="no-gutters mobile-display">
            <b-col cols="2">
              <b-button variant="primary" class="px-16 required-btn">{{
                $t("REQUIREDBUTTON")
              }}</b-button></b-col
            >
            <b-col cols="10"
              ><label class="highLightHeaderLabel pl-1">{{
                $t("PLEASEATTATCHEXPENSE")
              }}</label></b-col
            >
          </b-row>
          <div class="web-display">
            <b-button variant="primary" class="px-16 required-btn">{{
              $t("REQUIREDBUTTON")
            }}</b-button
            >&nbsp;&nbsp;
            <label class="highLightHeaderLabel pl-1">{{
              $t("PLEASEATTATCHEXPENSE")
            }}</label>
          </div>
        </b-col>
        <b-col cols="12" xl="12">
          <div v-if="files.length">
            <div
              id="upload-attach"
              v-for="(file, index) in files"
              v-bind:key="index"
              class="mb-2"
            >
              <img src="@/assets/file-icon.svg" class="attachment-icon-width" />
              {{ file.name }}
              <b-link
                @click="deleteFile(index)"
                href="javascript:;"
                class="card-link link"
              >
                <i class="fa fa-times text-danger"></i>
              </b-link>
            </div>
          </div>
          <b-form-file
            id="avatar"
            ref="file"
            @change="onFileChange"
            :placeholder="$t('ATTACHFILE')"
            drop-placeholder="$t('ATTACHFILE')"
            data-iconName="fa fa-upload"
            multiple="multiple"
          >
            <template slot="file-name" slot-scope="{}">
              {{ $t("ATTACHFILE") }}
            </template></b-form-file
          >
          <p class="text-danger">{{ fileErrorMessage }}</p>
        </b-col>
      </b-row>
      <b-col cols="12 mt-2">
        <div class="pr-2 date-fs line-4 text-center date-fw mobile-padding">
          {{ $t("SERVICEAGREETEXT") }}
        </div>
      </b-col>
      <b-col cols="12"
        ><b-form-checkbox v-model="status" size="sm" class="text-center"
          ><div class="date-fs date-fw">
            {{ $t("CONSENT") }}
          </div></b-form-checkbox
        >
      </b-col>

      <b-col cols="12" class="text-center mt-1">
        <b-button
          :disabled="!status || disabled"
          type="submit"
          variant="primary"
          class="send-btn mt-1"
          >{{ $t("SEND") }}
          <i v-if="loading" class="fa fa-spinner fa-spin" />
        </b-button>
      </b-col>
    </b-form>
    <b-modal
      id="service-complete-modal"
      ref="service-complete-modal"
      :hide-footer="true"
      centered
      variant="secondary"
      modal-class="py-0"
      body-class="py-0"
      size="lg"
      footer-class="border-0"
      header-class="border-0 bg-white pd-0"
      button-size="sm"
      hide-header-close
    >
      <template v-slot:modal-header="{ cancel }">
        <div class="w-100 d-flex justify-content-end">
          <b-button
            @click="cancel"
            class="back-btn close-btn mt-2"
            variant="primary"
            >{{ $t("CLOSE") }}</b-button
          >
        </div>
      </template>
      <b-card class="pop-up-box">
        <b-row>
          <b-col>
            <div class="title-fw">{{ $t("SERVICESENT") }}</div>
            <div class="title-fw mb-4">{{ $t("WAITFORANSWER") }}</div>

            <div class="title-fw">
              {{ $t("SERVICEAGENCY") }} : {{ $t("REQUESTNO") }} [{{
                serviceNumber
              }}]
            </div>
            <div class="title-fw mb-4">
              {{ $t("SUBSTITUTE") }} : {{ year + month + "度" }}
            </div>
            <div class="title-fw">{{ $t("ATTACHMENT") }}</div>
            <template>
              <b-card class="mb-3">
                <div v-for="(item, index) in popupFile" v-bind:key="index">
                  <a v-text="item.name" href="javascript:;" />
                </div>
              </b-card>
            </template>
            <div class="title-fw mb-4">{{ $t("PLEASETELLUSREQUESTNO") }}</div>
          </b-col>
        </b-row>
      </b-card>
    </b-modal>
  </div>
</template>

<script>
import { Settings } from "luxon";
Settings.defaultLocale = "ja";
import { ApiServicesFactory } from "@/shared/apiServiceFactory";
const BasicInfoApis = ApiServicesFactory.get("basic");
import { mapGetters } from "vuex";
import { endPoint } from "@/shared/endPoint";

export default {
  name: "AgencyServiceRequest",
  components: {},
  data() {
    return {
      year: new Date().getFullYear() + this.$t("YEAR"),
      month:
        new Date().getMonth() < 9
          ? "0" + (new Date().getMonth() + 1) + this.$t("MONTH")
          : new Date().getMonth() + 1 + this.$t("MONTH"),
      getYearOptions: this.getYearList(),
      getMonthOptions: this.getMonthList(),
      fileErrorMessage: "",
      serviceNumber: "",
      files: [],
      popupFile: [],
      status: false,
      disabled: false,
      loading: false,
    };
  },

  computed: {
    ...mapGetters({
      client_details: "getUserDetails",
    }),
  },
  mounted() {},
  created() {},
  methods: {
    /**Function to compile -10+ years from current Year */
    getYearList() {
      let year = new Date().getFullYear();
      year -= 11;
      let years = [];
      for (let i = 0; i <= 20; i++) {
        year++;
        years.push(year + this.$t("YEAR"));
      }
      return years;
    },
    /**Function to compile and assign 12 months of the year */
    getMonthList() {
      let months = [];
      for (let i = 1; i <= 12; i++) {
        if (i < 10) {
          months.push("0" + i + this.$t("MONTH"));
        } else {
          months.push(i + this.$t("MONTH"));
        }
      }
      return months;
    },

    showSerivceModal() {
      this.$refs["service-complete-modal"].show();
    },
    hideServiceModal() {
      this.$refs["service-complete-modal"].hide();
    },
    /**Function to submit attachments and compiling data as FormData and posting to API */
    async submit() {
      if (!this.files.length) {
        return (this.fileErrorMessage = this.$t("PROVIDEFILE"));
      }
      this.loading = true;
      this.disabled = true;

      var formData = new FormData();
      for (let i = 0; i < this.files.length; i++) {
        formData.append("attachments[]", this.files[i]);
      }
      formData.append("request_input_agency_dt", this.getCurrentDate());
      formData.append(
        "input_monthly",
        this.year.slice(0, -1) + this.month.slice(0, -1)
      );
      formData.append("client_no", this.client_details.client_account_no);
      formData.append("work_sts_kbn", "01"); //TODO change value as docs
      formData.append("work_dt", this.getCurrentDate()); //TODO change value as docs
      formData.append("person_id", 0); //TODO change value as docs

      formData.append("create_id", this.client_details.client_account_no);
      formData.append("update_id", this.client_details.client_account_no);

      const { data } = await BasicInfoApis.postAPI(
        endPoint.EVALUATIONREADYEARLYCOSTINF_TREQUESTINPUTGENCY_TREQUESTATTACHEDFILEMANAGER,
        formData
      );
      this.loading = false;
      this.disabled = false;

      if (data.success) {
        this.serviceNumber = data.data[0];
        this.popupFile = this.files;
        this.files = [];
        this.status = false;
        this.showSerivceModal();
      }
    },
    /**Funtion for uploading browsed file from local and checking validation of browsed file */
    onFileChange(event) {
      this.fileErrorMessage = "";

      let fileLength = this.files.length;
      if (event.target.files.length + fileLength > 5) {
        this.fileErrorMessage = this.$t("MAXFILENO");
        return true;
      }
      let fileSize = 0;
      for (var i = 0; i < event.target.files.length; i++) {
        var ext = event.target.files[i].name.split(".").pop();
        if (ext == "sh" || ext == "exe") {
          this.fileErrorMessage = this.$t("CANTNOTUPLOADEXCECUTABLEFILE");
          return true;
        }
        fileSize += Math.round(event.target.files[i].size / 1024);
      }
      if (fileSize > 10240) {
        this.fileErrorMessage = this.$t("MAXFILESIZE");
        return true;
      }

      for (let i = 0; i < event.target.files.length; i++) {
        this.files.push(event.target.files[i]);
      }
    },
    /**Delete the uploaded file from UI */
    deleteFile(index) {
      this.files.splice(index, 1);
    },
    navigateToPropsal() {
      this.$router.push({ name: "reduction_proposal_preview" });
    },
    /**Function to modify date format based on required UI format */
    getCurrentDate() {
      let d = new Date();
      return d.getFullYear() + "-" + (d.getMonth() + 1) + "-" + d.getDate();
    },
  },
};
</script>

<style scoped lang="scss">
