  color: #fff;
  background-color: $accordian_background;
  border-color: $accordian_background;
}

.btn-info:hover {
  background-color: $accordian_background;
  border-color: $accordian_background;
}
.link {
  color: $primary;
  font-weight: 900;
}
.flex-display {
  display: flex;
}

.send-btn {
  min-width: 90px !important;
  padding: 5px 30px 5px 30px !important;
}

.line-5 {
