}

.close-btn {
  min-width: 40px;
  padding: 2px 15px 2px 15px;
}
.card-header .btn {
  border-radius: 0rem;
}
.card {
  border-radius: 0rem;
}
label {
  margin-bottom: 0rem;
}
.line-2 {
  line-height: 2;
}
.card-min-height {
